import { Component, OnInit } from '@angular/core';

import { Router } from '@angular/router';
import { Hero } from '../hero';
import { HeroService } from '../hero.service';



@Component({
    selector: 'app-heroes',
    templateUrl: './heroes.component.html',
    styleUrls: ['./heroes.component.css']
})


export class HeroesComponent implements OnInit {
    heroes: Hero[];
    selectedHero: Hero;

    constructor(
        private heroService: HeroService,
        private router: Router) { }

    getHeroes(): void {
        this.heroService
            .getHeroes()
            .then(heroes => this.heroes = heroes);
    }

    add(name: string): void {
        name = name.trim();
        if (!name) { return; }
        this.heroService.create(name)
            .then(hero => {
                this.heroes.push(hero);
                this.selectedHero = null;
            });
    }

    /*
    sweetalert
    https://cdn.xgqfrms.xyz/sweetalert/sweetalert.min.js
    https://learning.xgqfrms.xyz/JavaScript/libs/sweetAlert/index.html
    npm i -D sweetalert ??? dev
    import lib
    */
/*    suredelete(): void {
        let sd = document.getElementById('suredelete');
        const bok = () => {
            swal("Good job!", "You clicked the button!", "success");
        }
        sd.addEventListener("click", bok , false);
    }*/
    delete(hero: Hero): void {
        this.heroService
            .delete(hero.id)
            .then(() => {
                this.heroes = this.heroes.filter(h => h !== hero);
                if (this.selectedHero === hero) { 
                    this.selectedHero = null; 
                }
            });
    }

    ngOnInit(): void {
        this.getHeroes();
    }

    onSelect(hero: Hero): void {
        this.selectedHero = hero;
    }

    gotoDetail(): void {
        this.router.navigate(['/detail', this.selectedHero.id]);
    }
}



